import { mapState, mapMutations } from 'vuex';

export default {
	data() {
		return {
			time: 60,
			timer: null,
			is_code: false,
			phone: '',
			code: '',
			pwd: '',
			confirmPwd: '',
		}
	},
	
	methods: {
		// 跳转
		code_login(e) {
			this.type = 2;
		},
		pwd_login(e) {
			this.type = 1;
		},
		get_code(){
			var that = this;
			console.log(that.phone);
			console.log(that.checkMobile(that.phone));
			if(!that.checkMobile(that.phone)){
				that.Tips({ title: "请输入正确的手机号"});
				return;
			}
			if(that.timer == null){
				
				that.basePost(
					that.U({ c: 'login', a: 'get_register_code'}),
					{phone:that.phone},
					function(res) {
						that.is_code = true;
						that.timer = setInterval(that.countDown,1000);
					},
					function(res) {
						console.log(res);
					},
				);
			}
		},
		countDown(){
			var that = this;
			if(that.time > 0){
				that.time --;
			}else{
				clearInterval(that.timer);
				that.timer = null;
				that.time = 60;
				that.is_code = false;
			}
		},
		register(e) {
			var that = this;
			if(!that.checkMobile(that.phone)){
				that.Tips({ title: "请输入正确的手机号"});
				return;
			}
			if(that.code == ''){
				that.Tips({ title: "请输入验证码"});
				return;
			}
			if(that.pwd == ''){
				that.Tips({ title: "请输入密码"});
				return;
			}
			if(that.confirmPwd == ''){
				that.Tips({ title: "请输入确认密码"});
				return;
			}
			if(that.pwd != that.confirmPwd){
				that.Tips({ title: "两次输入的密码不一致"});
				return;
			}
			that.basePost(
				that.U({ c: 'login', a: 'register_by_mobile'}),
				{
					phone:that.phone,
					code: that.code,
					pwd: that.pwd,
				},
				function(res) {
					that.loginStore(res.data);
					that.Tips({ title: "注册成功！"},{tab:3 ,url :2})
				},
				function(res) {
					console.log(res);
				},
			);
		},
		//返回上一页
		back() {
			uni.navigateBack();
		},
		setPhone(e){
			this.phone =  e.detail.value;
		},
		setCode(e){
			this.code =  e.detail.value;
		},
		setPwd(e){
			this.pwd =  e.detail.value;
		},
		setConfirmPwd(e){
			this.confirmPwd =  e.detail.value;
		},
		...mapMutations(['loginStore'])
	}
}
